.parallax-height {
  height: 500px;
}
.image-text {
  background-color:rgba(17, 16, 20, 0.6);
  color: rgb(247, 217, 227);
  padding: 10px;
  position: absolute;
  top: 45%; 
  left: 50%; 
  -webkit-transform: translate(-50%,-50%); 
      -ms-transform: translate(-50%,-50%); 
          transform: translate(-50%,-50%); 
}



