.about {
  margin: 60px 100px;
}
h1 {
  text-align: center;
  font-family: 'Orbitron';
  margin: 0 0 40px 0;
  font-size: 45px;
}
h3 {
  font-size: 25px;
}
section {
  display: -ms-flexbox;
  display: flex;
}
.about-sections {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  margin: 0 auto;
  padding: 0 30px;
  width: 300px;
}
img {
  width: 100px;
}
.icon {
  margin: 0 auto;
}
@-webkit-keyframes icon-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes icon-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.icon:hover {
  -webkit-animation: icon-spin infinite 400ms linear;
          animation: icon-spin infinite 400ms linear;
}



